import React from 'react'
//import 'bootstrap/dist/css/bootstrap.min.css';
//import Nav from 'react-bootstrap/Navbar'
import './nav.css'

const Navbar = () => {
    return (
        <div className="wrapper d-flex align-items-stretch"> 
          <nav className="navbar navbar-expand-lg navbar-dark bg-dark" style={{width: '100%'}}>
             <a className="navbar-brand" href="#">
             
             </a>
              <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                  <span className="navbar-toggler-icon"></span>
              </button>

            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav mr-auto">
                <li className="nav-item active">
                  <a className="nav-link" href="#"><i class="fa fa-home" aria-hidden="true"></i>Home</a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="#">About Me</a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="#">Portfolio</a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="#">Testimonials</a>
                </li>
                <li className="nav-item dropdown">
                  <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    Get Social
                  </a>
                  <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                    <a className="dropdown-item" href="#"><i class="fa fa-github" aria-hidden="true"></i>Github</a>
                    <a className="dropdown-item" href="#"><i class="fa fa-linkedin-square" aria-hidden="true"></i>LinkedIn</a>
                    <div className="dropdown-divider"></div>
                    <a className="dropdown-item" href="#"><i class="fa fa-twitter" aria-hidden="true"></i>Twitter</a>
                  </div>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="#"><i class="fa fa-envelope" aria-hidden="true"></i>Contact Me</a>
                </li>
              </ul>
           </div>
        </nav>
    </div>
    )
}

export default Navbar
